<template>
    <div>
    <!--    -->
        <navig-bar></navig-bar>
    <!--    -->
        <div>
            <van-tabs>
                <van-tab title="">
                    <!---->
                    <van-search v-model="searchKey" clearable @search="onSearch" @clear="onClear" placeholder="" />
                    <!---->
                    <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
                        <van-list
                                v-model="loading"
                                :finished="finished"
                                finished-text=""
                                @load="onLoad"
                        >
                            <van-cell v-for="item in list" :key="item" :title="item.WorkOrderId" :value="item.RoomId"  @click="onclickWO(item.WorkOrderId)">
                                <template #icon>
                                    <van-image
                                            round
                                            width="45"
                                            height="45"
                                            src="http://imagemxc.test.upcdn.net//.png"
                                    >
                                    </van-image>
                                </template>
                                <template #label>
                                    <div  v-if="item.RepairmanId" style="color:#40FF40"></div>
                                    <div v-else style="color: #EE0000"></div>
                                </template>
                            </van-cell>
                        </van-list>
                    </van-pull-refresh>
                </van-tab>
                <van-tab title="">
                    <!---->
                    <van-search v-model="searchKey" clearable @search="onSearchComplaints" @clear="onClearComplaints" placeholder="" />
                    <!---->
                    <van-pull-refresh v-model="refreshingComplaints" @refresh="onRefreshComplaints">
                        <van-list
                                v-model="loadingComplaints"
                                :finished="finishedComplaints"
                                finished-text=""
                                @load="onLoadComplaints"
                        >
                            <van-cell v-for="item in listComplaints" :key="item" :title="item.ComplaintID" :value="item.ComplaintUser" @click="showReply(item)">
                                <template #icon>
                                    <van-image
                                            round
                                            width="45"
                                            height="45"
                                            src="http://imagemxc.test.upcdn.net//.png"
                                    >
                                    </van-image>
                                </template>
                                <template #label>
                                    <div v-if="item.replied=='false'" style="color: #EE0000"></div>
                                    <div v-else style="color: #40FF40"></div>
                                </template>
                            </van-cell>
                        </van-list>
                    </van-pull-refresh>
                </van-tab>
            </van-tabs>
            <!--    -->
            <van-popup v-model="showReplyPanel" position="bottom" @close="cancelReply" :style="{ height: '60%' }">
                <van-form @submit="submitReply">
                    <h4></h4>
                    <van-cell title=":" value=""></van-cell>
					<div style="color: #A9A9A9; float:left; margin-left: 32px; font-size: 14px" v-html="thisItem"></div>
                    <van-celltitle=":"value=""></van-cell>
					<van-list>
					<van-imagev-for="itemincomplaintImg":key="item"style="width:'90%"fit="contain":src="item.src"/>
					</van-list>
                    <van-field
                            v-model="editReply"
                            name=""
                            label=""
                            placeholder=""
                            :rules="[{ required: true, message: '' }]"
                    />
                    <van-button round block type="info" native-type="submit">
                        
                    </van-button>
                </van-form>
            </van-popup>
        </div>
    <!--    -->
        <tab-bar></tab-bar>
    </div>
</template>

<script>
    import tabBar from "../tabBar";
    import navigBar from "../navigBar";
    import Vue from 'vue';
    import {Toast} from 'vant';
	import axios from 'axios';
    Vue.use(Toast);
    export default {
        name: 'CSWorkOrder',
        components: {
            tabBar,
            navigBar
        },
        data(){
            return{
                workOrders: [
                    { WorkOrderId: '', RoomId: '', RepairmanId: "", ServiceId: "", WorkOrderItem: "", replied: ""},
                    ],
                list: [
                    
                ],
                complaints: [
                    { ComplaintID: '', ComplaintUser: '', ComplaintItem: '', replied: ''},
                   ],
                listComplaints: [

                ],
                searchKey: '',
                loading: false,
                finished: false,
                refreshing: false,
                loadingComplaints: false,
                finishedComplaints: false,
                refreshingComplaints: false,
                showReplyPanel: false,
                thisItem: '',
                editReply: '',
				complaintImg:[{src:''},{src:''},{src:''},{src:''},{src:''},{src:''},{src:''},{src:''}],
				nowId:'',
            }
        },
        methods: {
            onSearch(){
                var _this = this;
				  axios.get('/searchworkorder',{
				params: {
				  Key: _this.searchKey,
				}
				})
				.then(function (response){
					console.log(response);
                    setTimeout(() => {
					_this.list=[];
                    for (let i = 0; i < response.data.obj.length; i++) {
						_this.workOrders[0]= response.data.obj[i];
                        _this.list.push(_this.workOrders[0]);
                    }
                    // 
                    _this.loading = false;
                    _this.finished = true;

                    // 
                    // if (this.list.length >= this.actions.length) {
                    // this.finished = true;
                    // }
                }, 1000);
				})
				.catch(function (error) { // 
				  console.log(error);
				});
            },
            onClear(){
                this.searchKey='';
                this.refreshing=true;
                this.onRefresh();
            },
            onLoad() {
                var _this = this;
				  axios.get('/workorderlist',{
				})
				.then(function (response){
					console.log(response);
                    setTimeout(() => {
                    for (let i = 0; i < response.data.obj.length; i++) {
						_this.workOrders[0]= response.data.obj[i];
                        _this.list.push(_this.workOrders[0]);
                    }
                    // 
                    _this.loading = false;
                    _this.finished = true;

                    // 
                    // if (this.list.length >= this.actions.length) {
                    // this.finished = true;
                    // }
                }, 1000);
				})
				.catch(function (error) { // 
				  console.log(error);
				});
            },
            onRefresh() {
                if (this.refreshing) {
                    this.list = [];
                    this.refreshing = false;
                }

                // 
                this.finished = false;

                // 
                //  loading  true
                this.loading = true;
                this.onLoad();
            },
            onclickWO(woID){
                this.$router.push({path: '/CS/CSWorkOrders/'+woID});
            },
            onSearchComplaints(){
			var _this = this;
				  axios.get('/csearch',{
				params: {
				  Key: _this.searchKey,
				}
				})
				.then(function (response){
					console.log(response);
                    setTimeout(() => {
					this.listComplaints=[];
                    for (let i = 0; i < response.data.obj.length; i++) {
						_this.complaints[0]= response.data.obj[i];
                        _this.listComplaints.push(_this.complaints[0]);
                    }
                    // 
                    _this.loadingComplaints = false;
                    _this.finishedComplaints = true;

                    // 
                    // if (this.list.length >= this.actions.length) {
                    // this.finished = true;
                    // }
                }, 1000);
				})
				.catch(function (error) { // 
				  console.log(error);
				});
            },
            onClearComplaints(){
                this.searchKey='';
                this.refreshingComplaints=true;
                this.onRefreshComplaints();
            },
            onLoadComplaints() {
				var _this = this;
				  axios.get('/clist',{
				})
				.then(function (response){
					console.log(response);
                    setTimeout(() => {
                    for (let i = 0; i < response.data.obj.length; i++) {
						_this.complaints[0]= response.data.obj[i];
                        _this.listComplaints.push(_this.complaints[0]);
                    }
                    // 
                    _this.loadingComplaints = false;
                    _this.finishedComplaints = true;

                    // 
                    // if (this.list.length >= this.actions.length) {
                    // this.finished = true;
                    // }
                }, 1000);
				})
				.catch(function (error) { // 
				  console.log(error);
				});
            },
            onRefreshComplaints() {
                if (this.refreshingComplaints) {
                    this.listComplaints = [];
                    this.refreshingComplaints = false;
                }

                // 
                this.finishedComplaints = false;

                // 
                //  loading  true
                this.loadingComplaints = true;
                this.onLoadComplaints();
            },
            showReply(item){
				var _this = this;
				console.log(item);
				axios.get('/cinitial',{
				params: {
				  CId:item.ComplaintID,
				}
			  })
			  .then(function (response){
				  console.log(response);
				  if(response.data.success == true){
					for (let i = 0; i < response.data.pic.length; i++) {
						_this.complaintImg[i].src= response.data.pic[i];
                    }
					_this.nowId=item.ComplaintID;
					_this.showReplyPanel = true;
					_this.thisItem=response.data.obj[0];
				  }
			  })
			  .catch(function (error) { // 
				console.log(error);
			  }); 
            },
            cancelReply(){
                this.showReplyPanel = false;
            },
            submitReply(){
				var _this = this;
				axios.get('/creply',{
				params: {
				  CId:_this.nowId,
				  Reply:_this.editReply,
				  Sid:localStorage.getItem('semail'),
				}
			  })
			  .then(function (response){
				  console.log(response);
				  if(response.data.success == true){
					_this.showReplyPanel = false;
					_this.refreshingComplaints=true;
					_this.onRefreshComplaints();
					Toast.success("");
				  }
			  })
			  .catch(function (error) { // 
				console.log(error);
			  }); 
                
            }
        }
    }
</script>

<style scoped>

</style>


