<template>
  <div class="Renting">   
      <h1>{{roomid}}</h1>
      <h1>{{money}}</h1>

      <van-picker
      title=""
      :default-index="0"
      show-toolbar
      :columns="columns"
      @confirm="onConfirm"
      @cancel="onCancel"
      @change="onChange"/> 

      <h1>{{time}}</h1>
      <van-cell is-link @click="showPopup"></van-cell>
      <van-popup
      v-model="show"
      closeable
      style="white-space: pre-wrap;"
      position="bottom"
      :style="{ height: '50%' }"><span style="font-size:25px;">{{hetong}}</span></van-popup>

      <p>{{msg}}</p>
      <van-button type="primary" @click="pay" :disabled="disabled"></van-button>
      <p><van-button type="primary" to="/Search"></van-button></p>

  </div>
</template>

<script>
import { Toast } from 'vant';
import axios from 'axios';

export default {
  name: 'Renting',
  data () {
    return {
        roomid:'',
        time:'3',
        money:'',
        timeindex:0,
        disabled:false,
        msg:'',
        show: false,
        hetong:'\n:\n:\n:\n:\n:\n:\n:',
        columns: ['3','','','','1', '2', '3', '4', '12'],
		myid:localStorage.getItem('email'),
		success:null,
    }
  },
  methods:{
      onConfirm(value, index) {
          this.time=this.columns[index];
      },
      onChange(picker, value, index) {
          this.time=this.columns[index];
          this.timeindex=index;
      },
      onCancel() {
      },
      showPopup() {
          this.show = true;
      },
      pay(){
          this.msg='';
          this.disabled=true;
		  var _this = this;
		  axios.get('/pay',{
			params:{
				user:_this.myid,
				roomid:_this.roomid,
				type:_this.timeindex,
			}
		  })
		  .then(function(response){
			_this.success = response.data;
			if(response.data == false){
				Toast('');
			}
			else{
				Toast('');
			}
		  })
		  .catch(function(error){
			console.log(error);
		  });
      }
  },
  created(){
      this.roomid = this.$route.query.roomid;
      this.money=this.$route.query.money;
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



