<template>
    <div>
        <!---->
        <div v-bind:style="{top: hoverHeight+'px', left: hoverWidth+'px'}" style="position: fixed; z-index: 1" >
            <van-icon name="add-o" size="30px" @click="newPerson"></van-icon>
        </div>
        <van-search v-model="searchKey" clearable @search="onSearch" @clear="onClear" placeholder="" />
        <!---->
        <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
            <van-list
                    v-model="loading"
                    :finished="finished"
                    finished-text=""
                    @load="onLoad"
            >
                <van-cell v-for="item in list" :key="item" :title="item.title" title-style="text-align: left; margin-left: 20px" :icon="item.avatar" @click="onClickUser(item.id)">
                    <template #icon>
                        <van-image
                                round
                                width="45"
                                height="45"
                                src="http://imagemxc.test.upcdn.net//.png"
                        >
                        </van-image>
                    </template>
                    <template #label>
                        <div v-bind:style="{width: commentWidth+'px'}"><div class="van-ellipsis">{{item.label}}</div></div>
                    </template>
                </van-cell>
            </van-list>
        </van-pull-refresh>

        <!---->
        <van-popup round v-model="showNewPersonPanel" closeable position="bottom" :style="{ height: '50%' }">
            <h3></h3>
            <van-form @submit="onSubmit">
                <van-field
                        v-model="username"
                        name=""
                        label=""
                        placeholder=""
                        :rules="[{ required: true, message: '' }]"
                />
                <van-field
                        v-model="password"
                        type="password"
                        name=""
                        label=""
                        placeholder=""
                        :rules="[{ required: true, message: '' }]"
                />
                <van-field
                        v-model="cpassword"
                        type="password"
                        name=""
                        label=""
                        placeholder=""
                        :rules="[{ required: true, message: '' }]"
                />
				<van-field
v-model="realname"
name=""
label=""
placeholder=""
:rules="[{required:true,message:''}]"
/>
<van-field
v-model="phoneNumber"
name=""
label=""
placeholder=""
:rules="[{required:true,message:''}]"
/>
                <div style="margin: 16px;">
                    <van-button round block type="info" native-type="submit">
                        
                    </van-button>
                </div>
            </van-form>
        </van-popup>

        <!---->
        <van-action-sheet v-model="showUserPanel" :actions="actions" @select="onSelectUser" />

        <!---->
        <van-popup v-model="showDeletePanel" round style="height: 200px; width: 300px">
            <div style="margin-top: 40px; margin-bottom: 20px"></div>
            <div style="margin-bottom: 10px"><van-button round type="danger" style="width: 180px" @click="deleteSuccess"></van-button></div>
            <div><van-button round type="default" style="width: 180px" @click="deleteCancel"></van-button></div>
        </van-popup>
    </div>
</template>

<script>
    import {Toast} from "vant";
import axios from 'axios';
    export default {
        name: "RepairerList",
        data() {
            return{
                commentWidth: document.documentElement.clientWidth-100,
                hoverHeight: document.documentElement.clientHeight-150,
                hoverWidth: document.documentElement.clientWidth-50,
                showActions: false,
                loading: false,
                finished: false,
                refreshing: false,
                showNewPersonPanel: false, // 
                showUserPanel: false, // 
                showDeletePanel: false, // 
                width: 0,
				realname:'',
				phoneNumber:'',
                userList: [
                    {id: "", title: "", label: ""}
                ],
                list: [

                ],
                username: '',
                password: '',
                cpassword: '',
                actions: [
                    { name: '' },
                    { name: '' ,color: "#ff0000"},
                ],
                thisUser: '',
                searchKey: '',
            }
        },
        methods: {
            onLoad() {
                var _this = this;
				  axios.get('/repairmanlist',{
				})
				.then(function (response){
                    setTimeout(() => {
                    for (let i = 0; i < response.data.obj.length; i++) {
						_this.userList[0]= response.data.obj[i];
                        _this.list.push(_this.userList[0]);
                    }

                    // 
                    _this.loading = false;
                    _this.finished = true;

                    // 
                    // if (this.list.length >= this.actions.length) {
                    // this.finished = true;
                    // }
                }, 1000);
                
				})
				.catch(function (error) { // 
				  console.log(error);
				});
            },
            onSelectUser(item) {
                // 
                //  close-on-click-action 
                //Toast(item.name);
                if(item.name==""){
                    //Toast.success("");
                    this.toUserHome(this.thisUser);
                }
                else
                    this.showDeletePanel = true;
            },
            onclick(){
                this.showActions = true;
            },
            onSearch(){
                var _this = this;
				  axios.get('/searchrepairman',{
				params: {
				  Key: _this.searchKey,
				}
				})
				.then(function (response){
				   
                    setTimeout(() => {
                    _this.list=[];
					for (let i = 0; i < response.data.obj.length; i++) {
						_this.userList[0]= response.data.obj[i];
                        _this.list.push(_this.userList[0]);
                    }

                    // 
                    _this.loading = false;
                    _this.finished = true;

                    // 
                    // if (this.list.length >= this.actions.length) {
                    // this.finished = true;
                    // }
                }, 1000);
                
				})
				.catch(function (error) { // 
				  console.log(error);
				});
            },
            onClear(){
                this.searchKey='';
                this.refreshing=true;
                this.onRefresh();
            },
            onRefresh() {
                if (this.refreshing) {
                    this.list = [];
                    this.refreshing = false;
                }

                // 
                this.finished = false;

                // 
                //  loading  true
                this.loading = true;
                this.onLoad();
            },
            newPerson() {
                this.showNewPersonPanel = true;
            },
            onSubmit() {
				var _this = this;
				axios.get('/addrepairman',{
				params: {
				  UserName:_this. username,
                  Ps1:_this.password,
                  Ps2:_this.cpassword,
				  Phone:_this.phoneNumber,
				  Name:_this.realname,
				}
			  })
			  .then(function (response){
				  console.log(response);
				  if(response.data.success == true){
					Toast.success('');
                    _this.showNewPersonPanel = false;
                    // 
                    _this.refreshing = true;
                    _this.onRefresh();
				  }
				   else
                    Toast.fail('');
			  })
			  .catch(function (error) { // 
				console.log(error);
			  });      
            },
            onClickUser(UserId) {
                // 
                this.thisUser = UserId;
                this.showUserPanel = true;
            },
            deleteSuccess(){
				var _this = this;
				axios.get('/deleterepairman',{
				params: {
				  UserId:_this.thisUser,
				}
			  })
			  .then(function (response){
				  console.log(response);
				  if(response.data.success == true){
					Toast.success("");
					_this.showUserPanel = false;
					_this.showDeletePanel = false;
					// 
					_this.refreshing = true;
					_this.onRefresh();
				  }
			  })
			  .catch(function (error) { // 
				console.log(error);
			  });   
            },
            deleteCancel(){
                this.showDeletePanel = false;
            },
            toUserHome(userId){
                this.$router.push({ path: "/CS/repairerHomeSP/" + userId });
            }
        }
    }
</script>

<style scoped>
</style>


