<template>
    <div id='csorders'>
        <!--  -->
        <navig-bar></navig-bar>
        <!--  -->
        <div>
            <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
                <van-list
                        v-model="loading"
                        :finished="finished"
                        finished-text=""
                        @load="onLoad"
                >
                    <van-cell v-for="item in list" :key="item" :title="item.title" :value="item.value" :label="item.label" @click="onclick(item.title)">
                        <template #icon>
                            <van-image
                                    round
                                    width="45"
                                    height="45"
                                    src="http://imagemxc.test.upcdn.net// (1).png"
                            >
                            </van-image>
                        </template>
                        <template #label>
                            <div v-if="item.viewed=='waiting'" style="color: #A9A9A9"></div>
                            <div v-if="item.viewed=='accepted'" style="color: #40FF40"></div>
							<div v-if="item.viewed=='rejected'" style="color: #FF0000"></div>
							<div v-if="item.viewed=='renew'" style="color: #FF0000"></div>
                        </template>
                    </van-cell>
                </van-list>
            </van-pull-refresh>
        </div>

        <!--  -->
        <van-action-sheet v-model="show" :actions="actions" :color="actions.color"  @select="onSelect" />
        <!---->
        <van-popup v-model="showDeletePanel" style="width: 200px; height: 150px" @close="cancelDelete" round>
            <div style="font-size: 20px; color: #FF0000 ; margin-top: 30px"></div>
            <van-button type="danger" @click="submitDelete"></van-button>
            <van-button type="default" @click="cancelDelete"></van-button>
        </van-popup>
        <!--  -->
        <div>
            <tabBar></tabBar>
        </div>
    </div>
</template>

<script>
    import Vue from 'vue';
    import navigBar from '../navigBar';
    import tabBar from "../tabBar";
	import axios from 'axios';
    // import { NavBar } from 'vant';
    // Vue.use(NavBar);
    new Vue({
    });
    export default {
        name: 'CSOrders',
        el: '#csorder',
        components: {
            tabBar,
            navigBar
        },
        data() {
            return {
                show: false,
                actions: [
                    { name: '' },
                    { name: '' ,color: "#ff0000"},
                ],
                testlist: [
                    {title: "O12560",value: "15-433", viewed: "0"}
                ],
                list: [

                ],
                loading: false,
                finished: false,
                refreshing: false,
                thisOrder: '',
                showDeletePanel: false,
            }
        },
        methods: {
            onLoad() {
                 var _this = this;
				  axios.get('/orderslist',{
				})
				.then(function (response){
                    setTimeout(() => {
                    for (let i = 0; i < response.data.obj.length; i++) {
						_this.testlist[0]= response.data.obj[i];
                        _this.list.push(_this.testlist[0]);
                    }

                    // 
                    _this.loading = false;
                    _this.finished = true;

                    // 
                    // if (this.list.length >= this.actions.length) {
                    // this.finished = true;
                    // }
                }, 1000);
                
				})
				.catch(function (error) { // 
				  console.log(error);
				});
            },
            onSelect(item) {
                // 
                //  close-on-click-action 
                this.show = false;
                // Toast(item.name);
                if(item.name=='')
                    this.$router.push("/CS/CSOrders/"+this.thisOrder);
                else{
                    this.show=false;
                    this.showDeletePanel = true;
                }

            },
            onclick(orderId){
                this.show = true;
                this.thisOrder = orderId;
            },
            onRefresh() {
                if (this.refreshing) {
                    this.list = [];
                    this.refreshing = false;
                }

                // 
                this.finished = false;

                // 
                //  loading  true
                this.loading = true;
                this.onLoad();
            },
            cancelDelete(){
                this.showDeletePanel =false;
            },
            submitDelete(){
				 var _this = this;
				axios.get('/deleteorder',{
				params: {
				  OrderId:_this.thisOrder,
				}
			  })
			  .then(function (response){
				  console.log(response);
				  if(response.data.success == true){
					 _this.showDeletePanel = false;
					 Toast.success("");
					 _this.refreshing=true;
				     _this.onRefresh();
				  }
			  })
			  .catch(function (error) { // 
				console.log(error);
			  });   
			  this.list = [];
			   this.finished = false;
			   this.loading = true;
			  this.onLoad();
            }
			
        }
    }
    import { Toast } from 'vant';
    Vue.use(Toast);
</script>

<style scoped>

</style>


