<template>
    <div>
        <h3></h3>
        <!--    -->
        <navig-bar></navig-bar>
        <!--    -->
        <van-cell-group>
            <van-cell title=":" :value="OrderId" icon="records"/>
            <van-cell title=":" :value="RoomId" icon="wap-home-o"/>
            <van-cell title=":" :value="RentalEmail" icon="manager-o"/>
            <van-cell title=":" :value="OrderType" icon="service-o"/>
            <van-cell title=":" :value="AuditStatus" icon="service-o">
                <template #default>
                    <div v-if="AuditStatus==''" style="color: #40FF40"></div>
                    <div v-else-if="AuditStatus==''" style="color: #A9A9A9"></div>
                    <div v-else style="color: #FF0000"></div>
                </template>
            </van-cell>
            <van-cell title=":" :value="OrderItem" icon="notes-o"/>
        </van-cell-group>
        <div v-if="AuditStatus==''">
            <van-button style="margin-top: 50px; " round block type="primary" @click="auditAccept"></van-button>
            <van-button style="margin-top: 20px; " round block type="danger" @click="auditDeny"></van-button>
        </div>

        <div v-if="AuditStatus==''">
            <van-button v-if="OrderType==''" style="margin-top: 20px; " round block type="primary" @click="exportContract"></van-button>
        </div>
		
        <!--    -->
        <tab-bar></tab-bar>

    </div>
</template>

<script>
    import navigBar from "../navigBar";
    import tabBar from "../tabBar";
    import Vue from 'vue';
	import axios from 'axios';
    import {Toast}from 'vant';
    Vue.use(Toast);
    export default {
        name: 'OrderSP',
        components: {
            navigBar,
            tabBar
        },
        created() {
            this.OrderId=this.$route.params.id;
			var _this = this;
				axios.get('/orderinitial',{
				params: {
				  OrderId:_this.OrderId,
				}
			  })
			  .then(function (response){
				  console.log(response);
				  if(response.data.success == true){
					_this.RoomId = response.data.obj[0];
					_this.RentalEmail = response.data.obj[1];
					_this.OrderType=response.data.obj[2];
					_this.AuditStatus=response.data.obj[3];
					_this.OrderItem=response.data.obj[4];
				  }
			  })
			  .catch(function (error) { // 
				console.log(error);
			  }); 
        },
        data(){
            return{
                OrderId: '',
                RentalEmail: 'shang@163.com',
                RoomId: 'R1321',
                OrderType: '',
                OrderItem: '',
                AuditStatus: '',
				ServiceId:localStorage.getItem('semail'),
            }
        },
        methods:{
            auditAccept(){
				var _this = this;
				axios.get('/acceptorder',{
				params: {
				  OrderId:_this.OrderId,
				  ServiceId:_this.ServiceId,
				}
			  })
			  .then(function (response){
				  console.log(response);
				  if(response.data.success == true){
					Toast.success("");
                    _this.AuditStatus='';
				  }
			  })
			  .catch(function (error) { // 
				console.log(error);
			  }); 
            },
            auditDeny(){
			var _this = this;
				axios.get('/rejectorder',{
				params: {
				  OrderId:_this.OrderId,
				  ServiceId:_this.ServiceId,
				}
			  })
			  .then(function (response){
				  console.log(response);
				  if(response.data.success == true){
					 Toast.success("");
					 _this.AuditStatus='';
				  }
			  })
			  .catch(function (error) { // 
				console.log(error);
			  }); 
            },
            exportContract(){
                this.$router.push("/CS/ExportContract/"+this.OrderId);
            }
        }
    }
</script>

<style scoped>

</style>


