<template>
    <div>
        <!---->
        <navig-bar></navig-bar>

        <div>
            <!--  -->
            <!--  -->
            <div style="margin-top:30%; margin-bottom: 10%">
                <van-image
                        round
                        width="120"
                        height="120"
                        v-bind:src=userAvatar
                ></van-image>
            </div>
            <!--ID-->
            <div id="showID">{{userName}}</div>
            <van-cell-group>
                <van-cell title=":" :value="userId" icon="records"/>
                <van-cell title=":" :value="userName" icon="user-o" @click="setName"/>
                <van-cell title=":" :value="userSex" icon="friends-o" @click="setSex"/>
                <van-cell title=":" :value="userPhoneNumber" icon="phone-o" @click="setPhoneNumber"/>
            </van-cell-group>
        </div>
        <!---->
        <van-popup v-model="showNameEdit" position="bottom" @close="cancelName" :style="{ height: '30%' }">
            <h5></h5>
            <van-form @submit="submitName">
                <van-field
                        v-model="newName"
                        name=""
                        label=""
                        placeholder=""
                        :rules="[{ required: true, message: '' }]"
                />
                <div style="margin: 16px;">
                    <van-button round block type="info" native-type="submit">
                        
                    </van-button>
                </div>

            </van-form>
        </van-popup>
        <!---->
        <van-popupv-model="showSexEdit"position="bottom"@close="cancelSex":style="{height:'30%'}">
<h5></h5>
<van-form@submit="submitSex">
<van-radio-groupv-model="newSex" direction="horizontal">
<van-radioname=""></van-radio>
<van-radioname=""></van-radio>
</van-radio-group>
<divstyle="margin:16px;">
<van-buttonroundblocktype="info"native-type="submit">

</van-button>
</div>
</van-form>
</van-popup>
        <!---->
        <van-popup v-model="showPhoneEdit" position="bottom" @close="cancelPhoneNumber" :style="{ height: '30%' }">
            <h5></h5>
            <van-form @submit="submitPhoneNumber">
                <van-field
                        v-model="newPhoneNumber"
                        name=""
                        label=""
                        placeholder=""
                        :rules="[{ required: true, message: '' }]"
                />
                <div style="margin: 16px;">
                    <van-button round block type="info" native-type="submit">
                        
                    </van-button>
                </div>

            </van-form>
        </van-popup>
        <tab-bar></tab-bar>
    </div>
</template>

<script>
    import Vue from 'vue';
    import navigBar from '../navigBar';
    import tabBar from "../tabBar";
	import axios from 'axios';
	import {Toast} from "vant";
    export default {
        name: 'userHomeSP',
        components: {
            navigBar,
            tabBar,
        },
        data() {
            return{
                userId: '',
                userName: '',
                userSex: '',
                userPhoneNumber: '12345678910',
                userAvatar: 'http://imagemxc.test.upcdn.net//.png',
                showNickEdit: false,
                showNameEdit: false,
                showSexEdit: false,
                showPhoneEdit: false,
                showAvatarEdit: false,
                newName: '',
                newSex: '',
                newPhoneNumber: '',
                columns: [
                    '', ''
                ],
                activeNames: ['1'],
            }
        },
        created() {
            this.userId = this.$route.params.id;
			var _this = this;
				axios.get('/rentinitial',{
				params: {
				  UserId:_this.userId,
				}
			  })
			  .then(function (response){
				  console.log(response);
				  if(response.data.success == true){
					_this.userName = response.data.obj[0];
					_this.userPhoneNumber = response.data.obj[1];
					_this.userSex=response.data.obj[2];
				  }
			  })
			  .catch(function (error) { // 
				console.log(error);
			  }); 
        },
        methods: {
            setName(){
                this.showNameEdit = true;
            },
            submitName(){
				var _this = this;
				axios.get('/changerentername',{
				params: {
				  UserId:_this.userId,
				  UserName:_this.newName,
				}
			  })
			  .then(function (response){
				  console.log(response);
				  if(response.data.success == true){
					_this.showNameEdit = false;
					Toast.success("");
					_this.userName = _this.newName;
				  }
			  })
			  .catch(function (error) { // 
				console.log(error);
			  }); 
            },
            cancelName(){
                this.showNameEdit = false;
            },
            setSex(){
                this.showSexEdit = true;
            },
            submitSex(value){
				var _this = this;
				axios.get('/changerentersex',{
				params: {
				  UserId:_this.userId,
				  UserSex:_this.newSex,
				}
			  })
			  .then(function (response){
				  console.log(response);
				  if(response.data.success == true){
					_this.showSexEdit = false;
					Toast.success("");
					_this.userSex = _this.newSex;
				  }
			  })
			  .catch(function (error) { // 
				console.log(error);
			  }); 
            },
            cancelSex(){
                this.showSexEdit = false;
            },
            setPhoneNumber(){
                this.showPhoneEdit = true;
            },
            submitPhoneNumber(){
				var _this = this;
				axios.get('/changerenterphone',{
				params: {
				  UserId:_this.userId,
				  UserPhone:_this.newPhoneNumber,
				}
			  })
			  .then(function (response){
				  console.log(response);
				  if(response.data.success == true){
					_this.showSexEdit = false;
					Toast.success("");
					_this.userPhoneNumber = _this.newPhoneNumber;
				  }
			  })
			  .catch(function (error) { // 
				console.log(error);
			  }); 
            },
            cancelPhoneNumber(){
                this.showPhoneEdit = false;
            },
            afterRead(){
                Toast.success("");
            },
        }
    }
    //import {Toast} from 'vant';
    Vue.use(Toast);
</script>

<style scoped>

</style>


